								<span class='guessing-order-item-price-item-name'>止损价</span>
								<span class='guessing-order-item-price-item-value'>{{item.fill_price | toFixedNum(2)}}</span>
							</div>
						</div>
						<div class="guessing-order-item-price">
							<div class='guessing-order-item-price-item'>
								<span class='guessing-order-item-price-item-name'>幸运数</span>
								<span class='guessing-order-item-price-item-value'>{{item.lucky_number}}</span>
							</div>
							<div class='guessing-order-item-price-item'>
								<span class='guessing-order-item-price-item-name'>幸运奖金</span>
								<span class='guessing-order-item-price-item-value'>{{item.lucky_usdt | toFixedNum(2)}}</span>
							</div>
						</div>
					</div>
				</div>
				<!-- 没有记录 -->
				<div v-if="guessingOrderList.length == 0" class="no-guessing-list">暂无订单</div>
			</div>
		</div>
		<!-- 底部按钮栏目 -->
		<div class="bottom">
			<van-row>
				<!-- <van-col class="guess-have" span="6">
					<div @click="showGuessingOrderModalFun">持仓</div>
				</van-col> -->
				<van-col class="guess-up" span="12">
					<div class="guess-up-box">
						<div class="guess-up-box-shadow" v-if="guess_data.is_open == 0"></div>
						<div @click="showGuessModalFun(1)">看涨</div>
					</div>
				</van-col>
				<van-col class="guess-down" span="12" >
					<div class="guess-down-box">
						<div class="guess-down-box-shadow" v-if="guess_data.is_open == 0"></div>
						<div @click="showGuessModalFun(2)">看跌</div>
					</div>
				</van-col>
			</van-row>	
		</div>

		<!-- 模态框 -->
		<div class="modal-box" v-if="showGuessModal">
			<div class="modal-shadow"></div>
			<div class="modal-content">
				<!-- 标题 -->
				<div class="modal-content-title">
					<img class="modal-content-title-about" src="../assets/imgs/about.png" alt="" @click="goHomeRule">
					<img class="modal-content-title-close" src="../assets/imgs/close.png" alt="" @click="closeGuessModalFun">
					<div>
						<span v-if="guess_type == 1">BTC 看涨</span>
						<span v-if="guess_type == 2">BTC 看跌</span>
					</div>
				</div>
				<!-- 竞猜金额 -->
				<div class="modal-content-item">
					<div class="modal-content-item-name">竞猜金额（USDT）</div>
					<div class="modal-content-item-box">
						<div class="modal-content-item-content">
							<span v-for="(item,index) in guess_data.usdt" :key="index" :class="{'active':guess_money_index == index}" @click="changeGuessMoney(item,index)">{{item.usdt_num}}</span>
						</div>
					</div>
				</div>
				<!-- 金额 -->
				<div class="modal-content-user-info">
					<div class="modal-content-user-info-left">
						USDT 余额：{{user_info.dw_money}}
						<span @click="goRecharge">充值</span>
					</div>
					<div class="modal-content-user-info-right">
						手续费：{{guess_data.recharge}}%
					</div>
				</div>
				<!-- 竞猜数量 -->
				<div class="modal-content-item">
					<div class="modal-content-item-name">竞猜数量</div>
					<div class="modal-content-item-box">
						<div class="modal-content-item-content">
							<span v-for="(item,index) in guess_data.quiz" :key="index" :class="{'active':guess_count_index == index}" @click="changeGuessCount(item,index)">{{item.quiz_num}}手</span>
						</div>
						
					</div>
				</div>
				<!-- 止盈/止损 -->
				<div class="modal-content-item">
					<div class="modal-content-item-name">止盈/止损</div>
					<div class="modal-content-item-box">
						<div class="modal-content-item-content">
							<span v-for="(item,index) in guess_data.point" :key="index" :class="{'active':guess_stop_index == index}" @click="changeGuessStop(item,index)">{{item.point_num}}</span>
						</div>
						
					</div>
				</div>
				<!-- 下单按钮 -->
				<div class="modal-content-btn">
					<van-row>
						<van-col class="modal-content-btn-left" span="12">
							<div class="modal-content-btn-left-total">总计：{{order_data.order_money}} USDT</div>
							<div class="modal-content-btn-left-tip">包含2%手续费</div>
						</van-col>
						<van-col class="modal-content-btn-right" span="12" >
							<div @click="confirm" :class="{'modal-content-btn-right-down':guess_type == 2}">确认下单</div>
						</van-col>
					</van-row>		
				</div>
			</div>
		</div>	

		<!-- 持仓模态框 -->
		<div class="modal-box" v-if="showGuessingOrderModal">
			<div class="modal-shadow"></div>
			<div class="modal-content">
				<!-- 标题 -->
				<div class="modal-content-title">
					<!-- <img class="modal-content-title-about" src="../assets/imgs/about.png" alt="" @click="goRule"> -->
					<img class="modal-content-title-close" src="../assets/imgs/close.png" alt="" @click="closeGuessingOrderModalFun">
					<div>持仓</div>
				</div>	
				<!-- 持仓订单 -->
				<div class="guessing-order-box">
					<div class="guessing-order-item" v-for="(item,index) in guessingOrderList" :key="index">
						<div class="guessing-order-item-title">
							<div class="guessing-order-item-title-type" :class="{'guessing-order-item-title-type-down':item.type == 2}">
								<span v-if="item.type == 1">看涨</span>
								<span v-if="item.type == 2">看跌</span>
							</div>
							<div class="guessing-order-item-title-name">{{item.name}}</div>
							<div>{{item.money}}USDT×{{item.count}}手</div>
						</div>
						<div class="guessing-order-item-price">
							<div class='guessing-order-item-price-item'>
								<span class='guessing-order-item-price-item-name'>下单价</span>
								<span class='guessing-order-item-price-item-value'>{{item.price}}</span>
							</div>
							<div class='guessing-order-item-price-item'>
								<span class='guessing-order-item-price-item-name'>当前价</span>
								<span class='guessing-order-item-price-item-value guessing-order-item-price-item-value-now'>5236</span>
							</div>
						</div>
						<div class="guessing-order-item-price">
							<div class='guessing-order-item-price-item'>
								<span class='guessing-order-item-price-item-name'>止盈价</span>
								<span class='guessing-order-item-price-item-value'>{{item.up_money}}</span>
							</div>
							<div class='guessing-order-item-price-item'>
								<span class='guessing-order-item-price-item-name'>止损价</span>
								<span class='guessing-order-item-price-item-value'>{{item.down_money}}</span>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>

import echartsDemo from '../components/EchartsDemo.vue'

import { mapState } from 'vuex'

export default {
	name: "home",
	components:{
		echartsDemo	
	},
	data(){
		return{
			isLogin:false,
			pageType:'',//页面模式 1有返回键 2没有返回键
			btn_price_data_before:'5012.21',//昨天btn收盘价格
			guess_money_index:0,
			guess_count_index:0,
			guess_stop_index:0,
			guess_data:{
				point:[],//止盈/止损
				quiz:[],//竞猜数量
				usdt:[],//竞猜金额
				recharge:0,//手续费
				btc_price:'',//昨天收盘价格
				is_open:0,
				opentime:{
					start_time:'',
					end_time:''
				},
				raward_list:[]
			},
			//下单数据
			order_data:{
				point:'',
				quiz:'',
				usdt:'',
				order_money:''
			},
			showGuessModal:false,//竞猜模态框
			guess_type:1,//1猜涨 2 猜跌
			showGuessingOrderModal:false,//持仓
			guessingOrderList:[
				// {type:1,name:'BTC',money:'200',count:3,up_money:'4396',down_money:'4436',price:'5321'},
				// {type:2,name:'BTC',money:'200',count:3,up_money:'4396',down_money:'4436',price:'5321'},
				// {type:1,name:'BTC',money:'200',count:3,up_money:'4396',down_money:'4436',price:'5321'},
				// {type:1,name:'BTC',money:'200',count:3,up_money:'4396',down_money:'4436',price:'5321'},
			],
			//实时价格
			btn_price_data:{
				vol:'',//成交量
				high:'',//最高
				low:'',//最低
				sell:'',//卖一价
				last:'',//最新成交价
				buy:''//买一价
			},

			//持仓定时器
			guessingInterval:null,


			//消息
			noticeList:[
				'1邓*棋在奖池中赢走666USDT',	
				'2邓*棋在奖池中赢走666USDT',	
				'3邓*棋在奖池中赢走666USDT',	
				'4邓*棋在奖池中赢走666USDT',	
				'5邓*棋在奖池中赢走666USDT',	
			],
			noticeInterval:null,
			animateNotice:false,

			//奖金池
			total_money:'0'
		}
	},
	filters:{
		//保留两位小数
		toFixedNum(val,num){
			if(val){
				return parseFloat(val).toFixed(num);
			}else{
				return ''
			}
			return val
		}
	},
	computed:{
		...mapState(['user_info']),//用户信息

		total_money_1(){
			if(this.total_money == ''){
				this.total_money = '0'
			}
			let totalMoney = parseInt(this.total_money) + '';
			this.utils.info('totalMoney...',totalMoney);
			if(totalMoney.length >= 6){
				return '9'
			}else if(totalMoney.length == 5){
				return totalMoney.substring(0,1)
			}else{
				return '0'
			}

			
		},
		total_money_2(){
			if(this.total_money == ''){
				this.total_money = '0'
			}
			let totalMoney = parseInt(this.total_money) + '';
			this.utils.info('totalMoney...',totalMoney);
			if(totalMoney.length >= 6){
				return '9'	
			}else if(totalMoney.length >= 4){
				return totalMoney.substring(totalMoney.length-4,totalMoney.length-3)
			}else{
				return '0'
			}

		},
		total_money_3(){
			if(this.total_money == ''){
				this.total_money = '0'
			}
			let totalMoney = parseInt(this.total_money) + '';
			this.utils.info('totalMoney...',totalMoney);
			if(totalMoney.length >= 6){
				return '9'		
			}else if(totalMoney.length >= 3){
				return totalMoney.substring(totalMoney.length-3,totalMoney.length-2)
			}else{
				return '0'
			}

		},
		total_money_4(){
			if(this.total_money == ''){
				this.total_money = '0'
			}
			let totalMoney = parseInt(this.total_money) + '';
			this.utils.info('totalMoney...',totalMoney);
			if(totalMoney.length >= 6){
				return '9'	
			}else if(totalMoney.length >= 2){
				return totalMoney.substring(totalMoney.length-2,totalMoney.length-1)
			}else{
				return '0'
			}

		},
		total_money_5(){
			if(this.total_money == ''){
				this.total_money = '0'
			}
			let totalMoney = parseInt(this.total_money) + '';
			this.utils.info('totalMoney...',totalMoney);
			if(totalMoney.length >= 6){
				return '9'		
			}else{
				return totalMoney[totalMoney.length - 1]
			}
			

		},
	},
	created(){
		
        if(this.$route.params.id){
			this.isLogin = true;
			this.utils.info('已登录...');
			this.user_info.user_id = this.$route.params.id;

			//获取用户信息
			this.getUserInfo(1);
		}else{
			this.utils.warn('未登录...');
		}


		this.utils.info('query...',this.$route.query);
		if(this.$route.query && this.$route.query.type){
			this.pageType = this.$route.query.type
		}else{
			this.pageType = '1'	
		}
		
		
	},
	mounted(){

		if(this.noticeInterval){
			clearInterval(this.noticeInterval);
		}
		this.noticeInterval = setInterval(this.scroll,2000)
	},
	beforeDestroy(){
		this.utils.info('组件即将销毁...');
		if(this.noticeInterval){
			clearInterval(this.noticeInterval);
		}
	},
	methods:{
		scroll(){
			if(this.guess_data.raward_list.length > 0){
				this.animateNotice=true;    
				setTimeout(()=>{      
						this.guess_data.raward_list.push(this.guess_data.raward_list[0]);
						this.guess_data.raward_list.shift();
						this.animateNotice=false; 
				},500)
			}
			
		},
		//返回app
		goBack(){
			this.utils.info('返回app...');
			this.$bridge.callhandler('goBack', {tag:"返回app"}, () => {})
		},
		//去充值
		goRecharge(){
			this.utils.info('去充值...');
			this.$bridge.callhandler('goRecharge', {tag:"去充值"}, () => {})	
		},
		//设置支付密码
		goPay(){
			this.utils.info('设置支付密码...');
			this.$bridge.callhandler('goPay', {tag:"设置支付密码"}, (data) => {})
		},
		//去登录
		goLogin(){
			this.utils.info('去登录...');
			this.$bridge.callhandler('goLogin', {tag:"去登录"}, (data) => {})
		},
		//获取用户信息 type 1 刷新页面数据 2 只刷新用户信息
		getUserInfo(type){
            let user_id = 'dy' + this.user_info.user_id + this.utils.randomNumber(4);
			this.http.post(this.http._HOST + 'login/user_detail',{user_id:user_id}).then(result =>{
                this.utils.info('获取用户信息...',result);
                if(result.success){
                    let data = result.data;

                    this.user_info.dw_money = data.dw_usdt;
                    this.user_info.pay_password = data.pay_password;
					this.user_info.token = data.token;

					if(type == 1){
						//获取页面数据
						this.getPageData();

						//持仓
						this.getGuessingList();
					}
					
					
                }else{
					this.utils.err('获取用户信息...err...3s后再次请求');
					let getUserInfoTimeout = setTimeout(() =>{
						this.getUserInfo(type);
						clearTimeout(getUserInfoTimeout);
					},3000)	
				}
			})
		},
		//接收子组件传值
		getDataFromChild(data){
			//this.utils.info('子组件传递的值...',data);

			this.btn_price_data = data;
		},
		//显示竞猜模态框
		showGuessModalFun(type){
			this.guess_type = type;
			this.showGuessModal = true;
		},
		//查看历史记录
		goHomeList(){
			if(!this.isLogin){
				this.utils.showToast('请先登录...');
				this.goLogin();
				return false;
			}
			this.$router.push({
				name:'homeList'
			})	
		},
		//查看玩法规则
		goHomeRule(){
			this.$router.push({
				name:'homeRule'
			})
		},
		//关闭竞猜模态框
		closeGuessModalFun(){
			this.showGuessModal = false;

			//初始化数据
			this.initOrderData();
		},
		//选择竞猜金额
		changeGuessMoney(item,index){
			
			if(this.guess_money_index == index){
				return false;
			}
			this.guess_money_index = index;
			this.order_data.usdt = item.usdt_num;
			this.order_data.order_money = (parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) + ((parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) * parseFloat(this.guess_data.recharge))/100
		},
		//选择竞猜数量
		changeGuessCount(item,index){
			
			if(this.guess_count_index == index){
				return false;
			}
			this.guess_count_index = index;
			this.order_data.quiz = item.quiz_num;
			this.order_data.order_money = (parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) + ((parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) * parseFloat(this.guess_data.recharge))/100
		},
		//选择止盈/止损
		changeGuessStop(item,index){
			
			if(this.guess_stop_index == index){
				return false;
			}
			this.guess_stop_index = index;
			this.order_data.point = item.point_num;
		},
		//确认下单
		confirm(){
			this.utils.info('确认下单...');
			if(!this.isLogin){
				this.utils.showToast('请先登录...');
				this.goLogin();
				return false;
			}
			this.utils.info('下单数据...',this.order_data);

			if(parseFloat(this.order_data.order_money) > parseFloat(this.user_info.dw_money)){
				this.utils.showToast('您的USDT余额不足');
				return false;
			}

			if(this.btn_price_data.sell == ''){
				this.utils.showToast('数据错误，请稍后再试');
				return false;
			}

			if(this.guessingOrderList.length >= 3){
				this.utils.showToast('您持仓数量已达到上限，请结算后再下单');
				return false;	
			}
			
			let option = {
				style:this.guess_type,
				buy_price:this.btn_price_data.sell,
				buy_quiz:this.order_data.quiz,
				usdt_fee:this.order_data.usdt,
				btc_point:this.order_data.point,
				token:this.user_info.token
			}
			this.utils.info('下单参数...',option);
			this.http.post(this.http._HOST + 'wave/buy_point',option).then(result =>{
				this.utils.info('下单...',result);
				if(result.success){
					this.utils.showToast('下单成功');
					
					//关闭竞猜模态框
					this.closeGuessModalFun();

					//刷新持仓
					this.getGuessingList();

					//刷新用户信息
					this.getUserInfo(2);
				}else{
					this.utils.showAlert(result.message);
				}
			})
		},
		//显示持仓模态框
		showGuessingOrderModalFun(){
			if(!this.isLogin){
				this.utils.showToast('请先登录...');
				this.goLogin();
				return false;
			}

			this.showGuessingOrderModal = true;
		},
		//关闭持仓模态框
		closeGuessingOrderModalFun(){
			this.showGuessingOrderModal = false;
		},
		//获取页面数据
		getPageData(){
			let option = {
				type:'2',
				token:this.user_info.token
			}
			this.http.post(this.http._HOST + 'wave/basic_usdt',option).then(result =>{
				this.utils.info('获取页面数据',result);
				if(result.success){
					this.guess_data = result.data;

					//初始化下单数据
					this.initOrderData();

				}else{
					this.utils.err('获取页面数据失败...3s后再次请求');
					let getPageDataTimeout = setTimeout(() =>{
						this.getPageData();
						clearTimeout(getPageDataTimeout);
					},3000)
				}
			})
		},
		//初始化下单数据
		initOrderData(){
			this.guess_money_index = 0;
			this.guess_count_index = 0;
			this.guess_stop_index = 0;
			if(this.guess_data.usdt.length > 0 && this.guess_data.quiz.length > 0 && this.guess_data.point.length > 0){
				this.order_data.usdt = this.guess_data.usdt[0].usdt_num;
				this.order_data.quiz = this.guess_data.quiz[0].quiz_num;
				this.order_data.point = this.guess_data.point[0].point_num;

				this.order_data.order_money = (parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) + ((parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) * parseFloat(this.guess_data.recharge))/100
			}else{
				this.utils.err('数据缺失...');
			}
		},
		//持仓
		getGuessingList(){
			let option = {
				token:this.user_info.token,
				type:'2'
			}
			this.http.post(this.http._HOST + 'wave/order_list',option).then(result =>{
				this.utils.info('持仓',result);
				if(result.success){
					this.guessingOrderList = result.data.order_list;
					this.total_money = result.data.btc_bonus;
					// if(this.guessingOrderList.length > 0){
					// 	this.guessingInterval = setInterval(() =>{

					// 	},5000)	
					// }

					if(this.guessingOrderList.length > 0){
						let guessingListTimeOut = setTimeout(() =>{
							this.utils.info('定时刷新持仓...');
							//刷新用户信息
							this.getUserInfo(2);
							//刷新持仓
							this.getGuessingList();
							clearTimeout(guessingListTimeOut);	
						},1000)
					}

				}else{
					this.utils.err('持仓...3s后再次请求');
					let getGuessingListTimeout = setTimeout(() =>{
						this.getGuessingList();
						clearTimeout(getGuessingListTimeout);
					},3000)	
				}
			})
		}
	}
};
</script>


